.price-backdrop {
  width: 100%;
  height: 100vh;
  z-index: 100;
  visibility: visible;
  opacity: 1;
  transition: opacity var(--modal-animation), visibility var(--modal-animation);
  background-color: #2e2f4266;
  position: fixed;
  top: 0;
  left: 0;
}

.price-backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.price {
  max-width: 414px;
  width: 100%;
  background-color: var(--primary-bg-color);
  border: 1px solid var(--accent-color);
  border-radius: 2px;
  padding: 45px 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.price .modal-window__buttons {
  margin-top: 25px;
}

.price__btn-close {
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.price__icon-close {
  stroke: #000;
  transition: stroke var(--animation);
}

.price__icon-close:hover, .price__icon-close:focus {
  stroke: var(--accent-color);
}

.price-form__title {
  font-family: var(--secondary-font);
  letter-spacing: .05em;
  color: #000;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
}

.price-form__box {
  margin-bottom: 25px;
}

.price-form__label {
  color: var(--accent-color);
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
  display: block;
}

.position-box {
  position: relative;
}

.price-form__input {
  width: 100%;
  height: 54px;
  border: 1px solid var(--accent-color);
  letter-spacing: .05em;
  -webkit-appearance: none;
  appearance: none;
  padding-left: 12px;
  font-size: 18px;
}

.price-form__input:hover + .price-form__input-icon, .price-form__input:focus + .price-form__input-icon {
