}

.price-form__input-icon {
  width: 24px;
  height: 24px;
  pointer-events: none;
  fill: var(--accent-color);
  transition: top var(--animation);
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(0, -50%);
}

.price-form__price {
  height: 30px;
  border: 1px solid var(--accent-color);
